//Navigation----------------------
let at3DShelf = getAt3DShelf();
let at2DFrame = getAt2DFrame();
let atAboutBoard = getAtAboutBoard();
let atGamesShelf = getAtGameShelf();
let atInProg = getAtInProg();

document.addEventListener('mousemove', onMouseMove, false);

function onMouseMove(event) {

    mouse.x = (event.clientX/window.innerWidth) * 2 - 1;
    mouse.y = (event.clientY/window.innerHeight) * 2 - 1;

    handleMouseMovement(mouse.x, mouse.y, cameraOS);
    mouse = getMouseVector2(event, window);
    const intersections = checkRayIntersections(mouse, camera, raycaster, scene);

    at3DShelf = getAt3DShelf();
    at2DFrame = getAt2DFrame();
    atAboutBoard = getAtAboutBoard();
    atGamesShelf = getAtGameShelf();
    atInProg = getAtInProg();

    if (at3DShelf) {
        addOutlineToModels(intersections, outlinePass);
    }
    else if (at2DFrame) {
        addOutlineTo2D(intersections, outlinePass);
    }
    else if (atAboutBoard) {
        addOutlineToAbout(intersections, outlinePass);
    }
    else if (atGamesShelf) {
        addOutlineToGames(intersections, outlinePass);
    }
    else if (atInProg) {
        addOutlineToInProg(intersections, outlinePass);
    }
    else {
        addOutlineBasedOnIntersections(intersections, outlinePass);
    }

}

document.addEventListener('mousedown', onMouseDown, false);

function onMouseDown(event){
    mouse = getMouseVector2(event, window);
    const intersections = checkRayIntersections(mouse, camera, raycaster, scene);

    animateCamera(
        intersections,
        camera,
        buttons,
        lod0Models,
        lod1Models,
        lod2Models,
        imagePanels,
        imageZoomPanels,
        aboutPanels,
        aboutButtons,
        games,
        inProgPanels,
        infoCards,
        linkButtons,
        cameraOS
    );

    animateModel(intersections, lod0Models, lod1Models, infoCards, linkButtons);

    animateImages(intersections, imagePanels, imageZoomPanels, infoCards, linkButtons);

    animateAbout(intersections, aboutPanels);

    animateGames(intersections, games, infoCards, linkButtons);

    animateInProg(intersections, inProgPanels, linkButtons);
}

//Animation-----------------------
function animate () {

    requestAnimationFrame(animate);
    let time = Date.now() * 0.00001
    sky[0].rotation.y = time;
    sky[0].rotation.z = 0.0005 * (1 + Math.sin(time));



    handleCameraRotation(camera, cameraOS, cameraMoving);
    composer.render();
}